  height: 50%;
  transform-origin: 100% 100%;
  background-color: #e7dfdd;
  position: absolute;
}

.cat-animation-mask:before {
  content: "";
  width: 100%;
  height: 100%;
  transform-origin: 0 100%;
  background-color: #e7dfdd;
  position: absolute;
  left: 100%;
}

.cat-animation-mask:after {
  content: "";
  width: 100%;
  height: 100%;
  transform-origin: 100% 0;
  background-color: #e7dfdd;
  position: absolute;
  top: 100%;
