.cat-mouth {
  width: 2px;
  height: 0;
  background-color: #fd7667;
  border: 2px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  animation: mouth-animation 2.5s 1s infinite;
  position: absolute;
