}

.cat-mouth:before {
  content: "";
  width: 2px;
  height: 2px;
  background-color: #eba764;
  border: 2px solid #514e51;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  position: absolute;
  top: 0;
  right: -4px;
}

.cat-mouth:after {
  content: "";
  width: 2px;
  height: 2px;
  background-color: #eba764;
