}

.cat-beard:after {
  content: "";
  width: 12px;
  height: 3px;
  background-color: #514e51;
  position: absolute;
  top: 5px;
  right: 0;
  transform: rotate(10deg);
}

.cat-beard.right {
  right: 52px;
}

.cat-beard.right:before {
  transform: rotate(10deg);
}

.cat-beard.right:after {
  transform: rotate(-10deg);
}

.cat-foot {
  width: 50%;
  height: 50%;
  transform-origin: 100% 0;
